import React, { useEffect, useState } from "react";
import {
  Button,
  Card,
  TextField,
} from "@mui/material";

import Autocomplete from "@mui/material/Autocomplete";
import { toast } from "react-toastify";

import { usePpiBankTransfer } from "./BankTransferPPIProvider";
import { getPpiBankList, addPpiBeneficiary, verifyPpiBeneficiary } from "../../api/login/Login";
import PpiPageLayout from "./PpiPageLayout";


const PpiAddBeneficiary = () => {
  const { setCurrentPage, setGlobalLoader } = usePpiBankTransfer();
  const [btnLoading, setBtnLoading] = useState(false);

  const [bankList, setBankList] = useState([]);
  const [mobile, setMobile] = useState("");
  const [showOtpModal, setShowOtpModal] = useState(false);
  const [location, setLocation] = useState({
    latitude: "",
    longitude: "",
  });

  const [otp, setOtp] = useState("");

  useEffect(() => {
    const savedMobile = localStorage.getItem("mobile")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  useEffect(() => {
    if (!navigator.geolocation) {
      toast.error("Geolocation is not supported by this browser");
      return;
    }

    navigator.geolocation.getCurrentPosition(
      (position) => {
        setLocation({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        });
      },
      (error) => {
        toast.error("Please allow location access");
        console.error(error);
      },
      {
        enableHighAccuracy: true,
        timeout: 10000,
        maximumAge: 0,
      }
    );
  }, []);


  const [form, setForm] = useState({
    name: "",
    accountNumber: "",
    ifsc: "",
    bankId: "",
  });

  const fetchBankList = async () => {
    try {
      const res = await getPpiBankList();
      setBankList(res?.data?.data || []);
    } catch {
      toast.error("Bank List Load Failed");
    }
  };

  useEffect(() => {
    fetchBankList();
  }, []);

  const handleSubmit = async () => {
    if (!form.name || !form.accountNumber || !form.ifsc || !form.bankId) {
      toast.error("All fields are required");
      return;
    }

    const payload = {
      name: form.name,
      accountNumber: form.accountNumber,
      ifsc: form.ifsc,
      bankId: form.bankId,
      mobile,
    };

    setBtnLoading(true);        //  ADD
    setGlobalLoader(true);

    try {
      const res = await addPpiBeneficiary(payload);
      const data = res?.data?.data;

      if (data?.statuscode === "OTP") {
        toast.success("OTP sent to registered mobile");
        setShowOtpModal(true);
      } else if (data?.statuscode === "TXN") {
        toast.success("Beneficiary Added Successfully!");
        setCurrentPage("ppi-status-page");
      } else {
        toast.error(data?.status || "Failed to add beneficiary");
      }
    } catch (err) {
      toast.error("Failed to add beneficiary");
    } finally {
      setBtnLoading(false);     //  ADD
      setGlobalLoader(false);
    }
  };

  const handleVerifyOtp = async () => {
    if (!otp || otp.length !== 6) {
      toast.error("Enter valid 6 digit OTP");
      return;
    }

    setBtnLoading(true);        //  ADD
    setGlobalLoader(true);

    try {
      const res = await verifyPpiBeneficiary({
        mobile,
        otp,
        latitude: location.latitude,
        longitude: location.longitude,
      });

      const data = res?.data?.data;

      if (data?.statuscode === "TXN") {
        toast.success("Beneficiary Verified Successfully");
        setShowOtpModal(false);
        setOtp("");
        setCurrentPage("ppi-status-page");
      } else {
        toast.error(data?.status || "OTP verification failed");
      }
    } catch {
      toast.error("OTP verification failed");
    } finally {
      setBtnLoading(false);     //  ADD
      setGlobalLoader(false);
    }
  };




  return (
    <PpiPageLayout title="Add Beneficiary">

      <div
        style={{
          minHeight: "calc(100vh - 64px)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Card
          sx={{
            p: { xs: 2, sm: 3 },
            borderRadius: "16px",
            backgroundColor: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
            border: "1.5px solid var(--bs-primary2)",
            boxShadow: "none",
            maxWidth: "400px",
          }}
        >
          <TextField
            fullWidth
            label="Registered Mobile Number"
            value={mobile}
            disabled
            sx={{ mb: 2 }}
            className="mui-autofill-fix"
          />

          <TextField
            fullWidth
            label="Beneficiary Name"
            name="name"
            sx={{ mb: 2 }}
            value={form.name}
            onChange={(e) => setForm({ ...form, name: e.target.value })}
            className="mui-autofill-fix"
          />

          <TextField
            fullWidth
            label="Account Number"
            name="accountNumber"
            sx={{ mb: 2 }}
            value={form.accountNumber}
            onChange={(e) => setForm({ ...form, accountNumber: e.target.value })}
            className="mui-autofill-fix"
          />

          <Autocomplete
            fullWidth
            options={bankList}
            getOptionLabel={(option) => option.bank_name}
            sx={{ mb: 2 }}
            onChange={(e, selected) =>
              setForm({
                ...form,
                bankId: selected?.bankID || "",
                ifsc: selected?.ifsc_code || "",
              })
            }
            renderInput={(params) => <TextField {...params} label="Search Bank" />}
            className="mui-autofill-fix"
          />

          <TextField
            fullWidth
            label="IFSC Code"
            name="ifsc"
            sx={{ mb: 2 }}
            value={form.ifsc}
            disabled
          />

          <Button
            fullWidth
            variant="contained"
            disabled={btnLoading}   //  ADD THIS
            sx={{
              borderRadius: "50px",
              backgroundColor: "var(--bs-primary2)",
              color: "var(--bs-primary1)",
              fontWeight: 700,
              mt: 1,
            }}
            onClick={handleSubmit}
          >
            {btnLoading ? "Processing..." : "Add Beneficiary"}
          </Button>


          <Button
            fullWidth
            sx={{
              borderRadius: "50px",
              mt: 1,
              color: "var(--bs-primary2)",
              fontWeight: 700,
            }}
            onClick={() => setCurrentPage("ppi-send-money")}
          >
            Cancel
          </Button>
        </Card>
      </div>
      {showOtpModal && (
        <>
          <div
            style={{
              position: "fixed",
              inset: 0,
              background: "rgba(0,0,0,0.45)",
              zIndex: 1200,
            }}
          />
          <Card
            sx={{
              position: "fixed",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              zIndex: 1300,
              p: 3,
              width: "90%",
              maxWidth: 360,
              backgroundColor: "var(--bs-primary1)",
              border: "1.5px solid var(--bs-primary2)",
              borderRadius: "16px",
            }}
          >
            <TextField
              fullWidth
              className="mui-autofill-fix"
              label="Enter OTP"
              value={otp}
              onChange={(e) => setOtp(e.target.value.replace(/\D/g, ""))}
              inputProps={{ maxLength: 6 }}
              sx={{ mb: 2 }}
            />

            <Button
              fullWidth
              variant="contained"
              disabled={btnLoading}   //  ADD THIS
              sx={{
                mb: 1,
                backgroundColor: "var(--bs-primary2)",
                borderRadius: "50px",
                color: "var(--bs-primary1)",
                fontWeight: "bold",
              }}
              onClick={handleVerifyOtp}
            >
              {btnLoading ? "Verifying..." : "Verify OTP"}
            </Button>


            <Button
              fullWidth
              onClick={() => {
                setShowOtpModal(false);
                setOtp("");
              }}
            >
              Cancel
            </Button>

          </Card>
        </>
      )}

    </PpiPageLayout>

  );
};

export default PpiAddBeneficiary;
