import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  Card,
  TextField,
  Typography,
  Dialog,
  DialogContent,
} from "@mui/material";

import { toast } from "react-toastify";
import { usePpiBankTransfer } from "./BankTransferPPIProvider";
import { dmtPpiRegistration, dmtPpiRegistrationVerify } from "../../api/login/Login";
import "./PpiTransactionReport.css";

const PpiAddCustomer = () => {
  const { setCurrentPage, setGlobalLoader } = usePpiBankTransfer();

  const [openOtpPopup, setOpenOtpPopup] = useState(false);
  const [otp, setOtp] = useState("");
  const [mobile, setMobile] = useState("");

  const [location, setLocation] = useState({ latitude: "0", longitude: "0" });
  const [btnLoading, setBtnLoading] = useState(false);


  useEffect(() => {
    const savedMobile = localStorage.getItem("mobile")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(
      (pos) => setLocation({
        latitude: pos.coords.latitude,
        longitude: pos.coords.longitude,
      }),
      () => {
        toast.warning("Location Disabled, Using Default Coords");
        setLocation({ latitude: "0", longitude: "0" });
      }
    );
  }, []);

  const [form, setForm] = useState({
    name: "",
    mobile: "",
    pan: "",
    aadhaarNumber: "",
  });

  const handleChange = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  const handleManualSubmit = async () => {
    if (btnLoading) return; // optional safety

    if (!form.name || !form.mobile || !form.pan || !form.aadhaarNumber) {
      return toast.error("All fields are required");
    }

    if (form.mobile.length !== 10) return toast.error("Enter valid 10-digit Mobile Number");
    if (form.aadhaarNumber.length !== 12) return toast.error("Enter valid 12-digit Aadhaar Number");

    setBtnLoading(true);        // 
    setGlobalLoader(true);

    try {
      const payload = {
        name: form.name,
        mobile: form.mobile,
        pan: form.pan,
        aadhaarNumber: form.aadhaarNumber,
        latitude: location.latitude,
        longitude: location.longitude,
      };

      await dmtPpiRegistration(payload);

      toast.success("OTP Sent Successfully");
      setOpenOtpPopup(true);

    } catch (err) {
      toast.error("Registration Failed");
    } finally {
      setBtnLoading(false);     // 
      setGlobalLoader(false);
    }
  };


  const handleVerifyOtp = async () => {
    if (btnLoading) return; // optional safety
    if (!otp) return toast.error("Enter OTP");

    setBtnLoading(true);        // 
    setGlobalLoader(true);

    try {
      const body = {
        mobile: form.mobile,
        otp,
        latitude: location.latitude,
        longitude: location.longitude,
      };

      const res = await dmtPpiRegistrationVerify(body);
      const statusCode = res?.data?.data?.statuscode;

      if (statusCode === "TXN") {
        toast.success("Customer Registration Completed");
        setOpenOtpPopup(false);
        setCurrentPage("ppi-status-page");
      } else if (statusCode === "RNF") {
        toast.info("Remitter Not Found, Please Register First");
        setOpenOtpPopup(false);
        setCurrentPage("ppi-add-customer");
      } else if (statusCode === "IST") {
        toast.error("Session Expired");
        setOpenOtpPopup(false);
        setCurrentPage("ppi-status-page");
      } else if (statusCode === "KYC") {
        toast.error("Mobile not linked with Aadhaar. Use Bio-auth to complete KYC");
        setOpenOtpPopup(false);
      } else {
        toast.error(res?.data?.data?.status || "Verification Failed");
      }

    } catch {
      toast.error("OTP Verification Failed");
    } finally {
      setBtnLoading(false);     // 
      setGlobalLoader(false);
    }
  };



  return (
    <Box p={2} sx={{ width: "100%", margin: "0 auto" }}>
      <Typography variant="h5" fontWeight={700} mb={3} textAlign="center" color="var(--bs-primary2)">
        Add Customer
      </Typography>

      <Card sx={{
        p: 3,
        borderRadius: "16px",
        backgroundColor: "var(--bs-primary1)",
        color: "var(--bs-primary2)",
        border: "1.5px solid var(--bs-primary2)",
        boxShadow: "none",
      }}>
        <TextField fullWidth label="Full Name" name="name" value={form.name}
          onChange={handleChange} sx={{ mb: 2 }} className="mui-autofill-fix" />

        <TextField fullWidth label="Mobile Number" name="mobile" value={form.mobile}
          onChange={(e) => setForm({ ...form, mobile: e.target.value.replace(/\D/g, "") })}
          inputProps={{ maxLength: 10 }} sx={{ mb: 2 }} className="mui-autofill-fix" />

        <TextField fullWidth label="PAN Number" name="pan"
          value={form.pan} onChange={(e) => setForm({ ...form, pan: e.target.value.toUpperCase() })}
          sx={{ mb: 2 }} className="mui-autofill-fix" />

        <TextField fullWidth label="Aadhaar Number" name="aadhaarNumber"
          value={form.aadhaarNumber}
          onChange={(e) => setForm({ ...form, aadhaarNumber: e.target.value.replace(/\D/g, "") })}
          inputProps={{ maxLength: 12 }} sx={{ mb: 3 }} className="mui-autofill-fix" />

        <Button
          fullWidth
          variant="contained"
          disabled={btnLoading}   // 
          sx={{
            borderRadius: "50px",
            padding: "10px",
            backgroundColor: "var(--bs-primary2)",
            color: "var(--bs-primary1)",
            fontWeight: 700,
          }}
          onClick={handleManualSubmit}
        >
          {btnLoading ? "Submitting..." : "Submit"}
        </Button>

      </Card>

      <Dialog
        open={openOtpPopup}
        onClose={() => {
          setOpenOtpPopup(false);
          setOtp("");
        }}
        fullWidth
        maxWidth="xs"
      >

        <DialogContent
          sx={{
            p: 3,
            backgroundColor: "var(--bs-primary1)",
            borderRadius: "16px",
            color: "var(--bs-primary2)",
            border: "1px solid var(--bs-primary2)",
          }}
        >
          <Typography textAlign="center" fontWeight={700} mb={2} fontSize={18}>
            Enter OTP
          </Typography>

          <TextField fullWidth label="OTP" value={otp}
            onChange={(e) => setOtp(e.target.value.replace(/\D/g, ""))}
            inputProps={{ maxLength: 6 }} className="mui-autofill-fix" />

          <Button
            fullWidth
            variant="contained"
            disabled={btnLoading}   // 
            sx={{
              mt: 3,
              borderRadius: "50px",
              backgroundColor: "var(--bs-primary2)",
              color: "var(--bs-primary1)",
              fontWeight: 700,
            }}
            onClick={handleVerifyOtp}
          >
            {btnLoading ? "Verifying..." : "Verify OTP"}
          </Button>

        </DialogContent>
      </Dialog>
    </Box>
  );
};

export default PpiAddCustomer;
